:root{
  --background: rgb(243, 243, 243);
  --text: rgb(138, 138, 138);
}

::-webkit-scrollbar{
  width: 0;
}

a{
  color: initial;
  text-decoration: none;
}   

.sticky_header{
  position: sticky;
  top: 0;
  height: 132px;
  width: calc(100vw);
  background: white;
  display: flex;
  flex-direction: column;
  z-index: 1;
  box-sizing: content-box;
}

.uppersection, .lowersection{
  margin: 15px 20px 0 20px;
  display: flex;
  align-items: center;
}

.uppersection{
  justify-content: space-between;
}

.logo img{
  height: 32px;
  width: auto;
}

.bar, .cart a{
  font-size: 16px;
}

.bar:hover, .cart:hover a{
  color: var(--text);
  transform: scale(1.2);
  transition: 0.2s;
}

.cart, .bar{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 32px;
  width: 32px;
  border-radius: 50%;
}

.cart{
  background-color: var(--background);
}

.lowersection{
  height: 45px;
  background-color: var(--background);
  border-radius: 16px;
}

.searchbar{
  width: calc(100% - 40px);
  height: 45px;
}

.searchbar input{
  height: 45px;
  width: 100%;
  border: none;
  outline: none;
  background: none;
  font-size: 16px;
}

.search-icon{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 45px;
  width: 45px;
  font-size: 16px;
  color: var(--text);
}

/* Slider beigins */

.slides{
  height: 132px;
  width: 75vw;
  margin: 0 5px;
}

.slides img{
  object-fit: cover;
  width: 75vw;
  height: 132px;
  border-radius: 15px;
}

/* Slider ends */

/* classes begins */

.classes_section{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 20px auto;
  width: 90vw;
}

.class{
  width: 18%;
  height: calc((90vw * 0.18) + 10px);
  margin: 10px 1%;
  display: flex;
  flex-direction: column;
}

.class img{
  width: 100%;
  height: calc(90vw * 0.18);
  border-radius: 10px;
  object-fit: cover;
}

.class_name{
  text-align: center;
  font-size: 12px;
}

.class_name a{
  color: var(--text);
}

/* classes ends */

/* novel Slider begins */

/* .novel_slider{
  display: flex;
  overflow-x: auto;
  align-items: center;
} */

.novel_text, .array_text{
  margin-left: 4vw;
  font-size: 22px;
}

.novel{
  margin: 0 4vw;
}

.novel img{
  object-fit: cover;
  width: 75vw;
  height: 120px;
  border-radius: 15px;
}

/* novel slider ends */

/* featured novel begins*/

.array{
  display: flex;
  flex-direction: column;
  justify-content:start;
  height: calc(28vw + 125px);
  width: 28vw;
  margin: 0 0 0 3vw;
  border-radius: 15px;
}

.array img{
  object-fit: cover;
  width: 28vw;
  height: 28vw;
  border-radius: 10px;
}

.array_discription{
  height: 55px;
  overflow: hidden;
  margin-bottom: 10px;
}

.array_price span:nth-child(1){
  color: rgb(58, 139, 245);
}

.array_price span:nth-child(2){
  margin-left: 10px;
  color: var(--text);
  text-decoration: line-through;
}
.array_discount{
  margin-top: 5px;
}

.array_discount{
  color: rgb(3, 209, 3);
}

/* featured novel ends */

/* Sticky icons begins */

.sticky_icon_section{
  position: sticky;
  bottom: 0;
  display: flex;
  height: 60px;
  width: 100vw;
  background: white;
  justify-content: space-around;
  align-items: center;
  border-radius: 30px 30px 0 0;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.2);
}

.icon{
  color: rgb(165, 165, 165);
  font-size: 25px;
}

/* Sticky icons ends */

/*checkbox begins*/

#check_bar{
  display: none;
}

/* checkbox ends */

/* bar begins*/

.bar_clicked{
  width: 0;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
}

.bar_div{
  width: 0;
  height: 100vh;
  overflow: hidden;
  background: rgb(255, 255, 255);
  box-shadow: 0 0 5px rgb(0, 0, 0);
}

.bar_image img{
  width: 100%;
  height: 150px;
  object-fit: cover;
}

.bar_icon{
  display: flex;
  padding: 15px 20px;
  align-items: center;
  color: rgb(78, 78, 78);
}

.bar_icon span:nth-child(2){
  padding-left: 20px;
}

.bar_link_section{
  border-top: 1px solid rgb(212, 212, 212);
  padding: 5px 0;
}

.extra_label label{
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1;
  background: rgba(0, 0, 0, 0.2);
  display: none;
}

/* bar ends */

/* when checkbox is checked */

#check_bar:checked ~ .bar_clicked > .bar_div{
  transition: 0.3s;
  width: 300px;
}

#check_bar:checked ~ .bar_clicked > .extra_label label{
  display: initial;
}

/* media query */

@media screen and (min-width: 345px) {
  .class_name{
    font-size: 14px;
  }
}

@media screen and (min-width: 478px) {
  .class_name{
    font-size: 16px;
  }

  .bar, .cart a{
    font-size: 18px;
  }

  .slider_section{
    margin: 10px 0;
  }

  .slides, .slides img, .novel img{
    height: 230px;
  }

  .class{
    margin: 20px 1%;
  }
}

@media screen and (min-width: 600px) {
  .class_name{
    font-size: 18px;
  }
}

@media screen and (min-width: 768px) {
  .class_name{
    font-size: 20px;
  }

  .sticky_header{
    height: 66px;
  }

  .bar, .cart a{
    font-size: 20px;
  }

  .slides, .slides img, .novel img{
    height: 300px;
  }

  .sticky_icon_section{
    position: fixed;
    top: 0;
    left: 65px;
    display: flex;
    margin-top: 15px;
    height: 36px;
    width: 220px;
    background: none;
    border-radius: 0;
    box-shadow: none;
    z-index: 1;
  }
  
  .icon{
    color: rgb(0, 0, 0);
    font-size: 20px;
  }

  .icon:hover{
    color: var(--text);
    transform: scale(1.2);
    transition: 0.2s;
  }

  .lowersection{
    margin: 15px 20px 0 20px;
    width: calc(20vw + 45px);
    height: 32px;
    position: fixed;
    top: 0;
    right: 64px;
  }
  
  .searchbar{
    height: 32px;
    width: calc(20vw);
  }
  
  .searchbar input{
    height: 32px;
    font-size: 16px;
  }
}

@media screen and (min-width: 1024px) {
  .class_name{
    font-size: 20px;
  }

  .slides, .slides img, .novel img{
    height: 420px;
  }
}